import React, { useEffect, useState } from 'react'
import { FoodItem } from '@/app/types/FoodItem';
import Image from 'next/image'
import Gallary from './Gallary';

const array: FoodItem[] = [
    {
        id: 1,
        head: 'Salad',
        tag: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry standard dummy text ever since the 1500s,'

    },
    {
        id: 2,
        head: 'Cake',
        tag: 'Lorem Ipsum has been the industry standard dummy text ever since the 1500s,'

    },
    {
        id: 3,
        head: 'Burger',
        tag: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry standard dummy text ever since the 1500s,'

    },
    {
        id: 4,
        head: 'Icecream',
        tag: 'Lorem Ipsum is simply dummy text industry. Lorem Ipsum has been the industry standard dummy text ever since the 1500s,'

    },
    {
        id: 5,
        head: 'Pasta',
        tag: 'Simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry standard dummy text ever since the 1500s,'

    },
    {
        id: 6,
        head: 'Cookies',
        tag: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry standard dummy text ever since the 1500s,'

    },

]

interface Category {
  idCategory: string;
  strCategory: string;
  strCategoryThumb: string;
  strCategoryDescription: string;
}

// Define the structure of the full API response
interface CategoriesResponse {
  categories: Category[];
}



const HomeScreen: React.FC = () => {

    const [categories, setCategories] = useState<Category[]>([]);
    const [loading, setLoading] = useState<boolean>(true);


     useEffect(() => {
    const fetchRecipes = async () => {
      try {
        const response = await fetch('https://www.themealdb.com/api/json/v1/1/categories.php', {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            Accept: 'application/json',
          },
        });
        const data = await response.json();
                console.log("Data",data)

        setCategories(data);
      } catch (error) {
        console.error('Error fetching recipes:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchRecipes();
  }, []);

  if (loading) return <p>Loading...</p>;

    return (
        <div className='flex flex-col justify-center w-full items-center mb-10'>
            <div className='flex bg-amber-500 relative w-screen h-screen'>
                <Image
                    src="/images/homeicon.jpg"
                    alt="HomeIcon"
                    fill
                    className="object-cover"
                    priority
                />
                <div className="absolute inset-0 flex flex-col items-center justify-center z-10 text-center px-4">
                    <span className="font-bold text-[46px] text-white">Healthy Food Recipes</span>
                    <span className="text-[16px] text-white mt-2 italic">
                    " Whether you're a seasoned chef or just learning to boil water, our recipes are crafted
                     to guide you through delicious meals made with love, ease, and real ingredients. "
                     </span>                
                </div>
            </div>


            <span className='m-8 font-bold font text-[34px]'>Taste Of Food</span>
            <div className='flex flex-wrap justify-center w-full items-center'>
                {
                    array.map((item: any) => {

                        return (
                            <div className='group w-100 h-30 text-black hover:bg-[#FF7518] hover:text-white m-3 flex flex-row justify-center items-center rounded-[.5vw] cursor-pointer'>

                                <div className='w-25'>
                                    <Image
                                        src="/"
                                        alt="Food Item"
                                        width={100}
                                        height={100}
                                        className="object-cover" />
                                </div>
                                <div className='w-75 flex flex-col'>
                                    <span className=' font-bold font text-[16px] group-hover:text-white'>{item?.head}</span>
                                    <span className='text-[#A9A9A9] font text-[12px] group-hover:text-white'>{item?.tag}</span>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
            <div className='w-100 flex justify-center items-center'>
                <button className='w-40 h-12 bg-[#FF7518] mt-6 text-white rounded-[1.5vw] cursor-pointer'>Read More</button>
            </div>
            <Gallary />
        </div>
    )
}

export default HomeScreen
